export default {
	name: 'tuiUpload',
	props: {
		//初始化图片路径
		value: {
			type: Array,
			default () {
				return []
			}
		},
		//禁用删除
		forbidDel: {
			type: Boolean,
			default: false
		},
		//禁用添加
		forbidAdd: {
			type: Boolean,
			default: false
		},
		//服务器地址
		serverUrl: {
			type: String,
			default: ""
		},
		//限制数
		limit: {
			type: Number,
			default: 9
		},
		//项目名，默认为 file
		fileKeyName: {
			type: String,
			default: "file"
		}
	},
	data() {
		return {
			//图片地址
			imageList: [],
			//上传状态：1-上传成功 2-上传中 3-上传失败
			statusArr: []
		}
	},
	created() {
		this.imageList = [...this.value];
		for (let item of this.imageList) {
			this.statusArr.push("1")
		}
	},
	computed: {
		isShowAdd() {
			let isShow = true;
			if (this.forbidAdd || (this.limit && this.imageList.length >= this.limit)) {
				isShow = false;
			}
			return isShow
		}
	},
	methods: {
		// 重新上传
		reUpLoad(index) {
			this.$set(this.statusArr, index, "2")
			this.change()
			this.uploadImage(index, this.imageList[index]).then(() => {
				this.change()
			}).catch(() => {
				this.change()
			})
		},
		change() {
			let status = ~this.statusArr.indexOf("2") ? 2 : 1
			if (status != 2 && ~this.statusArr.indexOf("3")) {
				// 上传失败
				status = 3
			}
			this.$emit('complete', {
				status: status,
				imgArr: this.imageList
			})
		},
		chooseImage: function() {
			let _this = this;
			uni.chooseImage({
				count: _this.limit - _this.imageList.length,
				success: function(e) {
					let imageArr = [];
					for (let i = 0; i < e.tempFilePaths.length; i++) {
						let len = _this.imageList.length;
						if (len >= _this.limit) {
							uni.showToast({
								title: `最多可上传${_this.limit}张图片`,
								icon: "none"
							});
							break;
						}
						let path = e.tempFilePaths[i]
						imageArr.push(path)
						_this.imageList.push(path)
						_this.statusArr.push("2")
					}
					_this.change()

					let start = _this.imageList.length - imageArr.length
					for (let j = 0; j < imageArr.length; j++) {
						let index = start + j
						//服务器地址
						if (_this.serverUrl) {
							_this.uploadImage(index, imageArr[j]).then(() => {
								_this.change()
							}).catch(() => {
								_this.change()
							})
						} else {
							//无服务器地址则直接返回成功
							_this.$set(_this.statusArr, index, "1")
							_this.change()
						}
					}
				}
			})
		},
		uploadImage: function(index, url) {
			let _this = this;
			return new Promise((resolve, reject) => {
				uni.uploadFile({
					url: this.serverUrl,
					name: this.fileKeyName,
					header: {
						//设置请求头
					},
					formData: {},
					filePath: url,
					success: function(res) {
						console.log(res)
						if (res.statusCode == 200) {
							//返回结果 此处需要按接口实际返回进行修改
							let d = JSON.parse(res.data.replace(/\ufeff/g, "") || "{}")
							//判断code，以实际接口规范判断
							if (d.code % 100 === 0) {
								// 上传成功 d.url 为上传后图片地址，以实际接口返回为准
								d.url && (_this.imageList[index] = d.url)
								_this.$set(_this.statusArr, index, d.url ? "1" : "3")
							} else {
								// 上传失败
								_this.$set(_this.statusArr, index, "3")
							}
							resolve(index)
						} else {
							_this.$set(_this.statusArr, index, "3")
							reject(index)
						}
					},
					fail: function(res) {
						_this.$set(_this.statusArr, index, "3")
						reject(index)
					}
				})
			})

		},
		delImage: function(index) {
			this.imageList.splice(index, 1)
			this.statusArr.splice(index, 1)
			this.$emit("remove", {
				index: index
			})
			this.change()
		},
		previewImage: function(index) {
			if (!this.imageList.length) return;
			uni.previewImage({
				current: this.imageList[index],
				loop: true,
				urls: this.imageList
			})
		}
	}
}
